.field {
  position: relative;

  &--phone::after {
    content: "";
    position: absolute;
    right: 0;
    height: 58px;
    width: 58px;
    background-color: $grey-dark;
    background-image: url("../img/form/phone.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
  }

  &--mail::after {
    content: "";
    position: absolute;
    right: 0;
    height: 58px;
    width: 58px;
    background-color: $grey-dark;
    background-image: url("../img/form/mail.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
  }
}

.field__label {
  padding-left: 16px;
  color: $light-black;
  font-size: 14px;
  line-height: 21px;

  &--custom {
    display: flex;
    align-items: center;
    padding: 12px 10px 12px 64px;
    font-size: 18px;
    line-height: 26px;
    font-weight: 700;
  }
}

.field__input {
  width: 100%;
}

.field__custom {
  display: block;
  position: absolute;
  left: 0;
  width: 46px;
  height: 46px;
  background-color: $grey-bg;
  cursor: pointer;

  &--radio {
    border-radius: 50%;
  }
}

.field__radio:checked + .field__custom--radio::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: $black;
}

.field__checkbox:checked + .field__custom--checkbox::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 22px;
  height: 16px;
  background-image: url("../img/form/tick.svg");
}
